#map {
	margin-bottom: 50px;
	@include media-breakpoint-down(md) {
		margin-bottom: 30px; }
	iframe {
		pointer-events: none;
		&.active {
			pointer-events: all; } } }

.contact-page {
	padding: 50px 0;
	@include media-breakpoint-down(md) {
		padding: 30px 0; } }
.contact-form {
	.form-group {
		margin-bottom: 15px; }
	label {
		@include fs(14rem);
		color: darken($white,60%);
		margin-bottom: 5px;
		position: relative;

		.fa {
			@include pos(abs, 0 null null 0);
			@include fs(16rem);
			color: $green;
			text-align: center;
			width: 30px;
			height: 30px;
			line-height: 30px;
			background: darken($white, 5%); } }
	input, textarea {
		background: none;
		width: 100%;
		border: none;
		padding: 10px 0;
		@include fs(15rem);
		border-bottom: 1px solid darken($white,20%);
		&:active, &:focus {
			border-color: $green; } }
	.btn-wrap {
		text-align: right; }
	input[type="submit"] {
		width: auto;
		padding: 0 20px;
		color: $white;
		background: $green;
		@include fs(16rem);
		border: none;
		@include bor-rad(2px);

		font-weight: 500;
		box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
		line-height: 40px;
		margin-top: 20px;



		&:hover {
			background: lighten($green,5%);
			box-shadow: 0 4px 5px 0 rgba(0,0,0,.14), 0 1px 10px 0 rgba(0,0,0,.12), 0 2px 4px -1px rgba(0,0,0,.2); } } }
.contact-detail {

	// box-shadow: 0 0px 40px rgba(0,0,0,0.1)

	ul {
		width: 100%;
		li {
			@include pos(rel,null);
			padding-left: 60px;
			margin-bottom: 30px;
			color: darken($white,40%);
			line-height: 1.7;
			width: 100%;
			display: block;
			.fa {
				@include pos(abs, 0 null null 0);
				@include fs(16rem);
				color: lighten($main, 10%);
				text-align: center;
				width: 40px;
				height: 40px;
				line-height: 38px;
				border: 1px solid darken($white, 10%);
				@include bor-rad(50%); }
			span {
				h3 {
					@include fs(20rem);
					color: $main;
					font-weight: 300;
					margin-bottom: 10px; }
				p {
					a {
						color: #999; } } }
			span {
				display: block;
				a {
					color: darken($white,60%);
					&:hover {
						color: $green; } } } } } }

.testimonial {
	margin-bottom: 70px;
	.test-title {
		@include fs(28rem);
		font-weight: 400;
		color: $main;
		margin-bottom: 30px;
		text-align: center;
		position: relative;
		padding-bottom: 30px;
		margin-bottom: 50px;
		text-transform: uppercase;
		@include media-breakpoint-down(md) {
			@include fs(22rem);
			padding-bottom: 10px;
			margin-bottom: 30px; }
		&:before {
			content: '';
			@include pos(abs, null null 0 calc(50% - 250px));
			width: 500px;
			height: 2px;
			border-bottom: 2px dashed $green;
			@include media-breakpoint-down(md) {
				width: 300px;
				left: calc(50% - 150px); } } }
	.test-item {
		figure {
			text-align: center;
			.item-img {
				@include bor-rad(50%);
				width: 200px;
				height: 200px;
				overflow: hidden;
				display: inline-block;
				margin-bottom: 20px;
				img {
					width: 100%; } }
			figcaption {
				.item-content {
					font-style: italic;
					color: $main;
					font-weight: 300;
					margin: auto;
					margin-bottom: 30px;
					@include fs(20rem);
					@include media-breakpoint-up(xl) {
						max-width: 60%; }
					@include media-breakpoint-down(sm) {
						@include fs(16rem); }

					p {
						font-weight: 300; } }
				.item-name {
					color: $main;
					font-weight: 700;
					@include fs(18rem);
					text-align: center;
					letter-spacing: 2px;
					@include media-breakpoint-down(sm) {
						@include fs(16rem); } } } } }
	.slick-next, .slick-prev {
		@include fs(0);
		@include pos(abs, 50% 150px null null);
		@include transform(translateY(-50%));
		z-index: 10;
		@include media-breakpoint-down(lg) {
			right: 0px; }
		@include media-breakpoint-down(xs) {
			bottom: -50px;
			right: 30%;
			top: unset;
			@include transform(translateY(0));
			&:before {
				background: $white;
				color: $blue; } }
		&:before {
			content: '\e876';
			font-family: 'Linearicons-free';
			@include fs(20rem);
			width: 45px;
			height: 45px;
			line-height: 45px;
			background: $white;
			color: #666;
			font-weight: 700;
			display: inline-block;
			@include transition(all 0.3s);
			@include bor-rad(2px);
			@include media-breakpoint-down(lg) {
				width: 35px;
				height: 35px;
				line-height: 35px; } }
		&:hover {
			&:before {
				background: $green;
				color: $white; } } }
	.slick-prev {
		left: 150px;
		&:before {
			content: '\e875'; }
		@include media-breakpoint-down(lg) {
			left: 0; }
		@include media-breakpoint-down(xs) {
			bottom: -50px;
			left: 30%; } } }
