$facebook: #3a579b;
$gmail: #da4831;
$twitter: #14c5f9;
.footer {
	text-align: center;
	background: #000;
	height: 60px;
	@include media-breakpoint-down(md) {
		height: unset;
		line-height: 1.3; }
	@include media-breakpoint-down(md) {
		padding-bottom: 20px; }
	@include media-breakpoint-down(xs) {
		padding: 20px 0; }
	.company {
		float: left;
		color: #999;
		text-transform: uppercase;
		@include fs(14rem);
		line-height: 60px;

		@include media-breakpoint-down(xs) {
			float: none;
			display: inline-block;
			line-height: 1.3; } }


	.footer-social {
		line-height: 60px;
		display: inline-block;
		a {
			display: inline-block;
			@include fs(18rem);
			color: $white;
			width: 35px;
			height: 35px;
			text-align: center;
			line-height: 35px;
			margin: 0 5px;
			@include bor-rad(50%);
			&.facebook {
				background: $facebook; }
			&.gmail {
				background: $gmail; }
			&.twitter {
				background: $twitter; }
			&:hover {
				@include transform(translateY(-5px)); } } }
	.copy {
		float: right;
		color: #999;
		text-transform: uppercase;
		@include fs(14rem);
		line-height: 60px;
		@include media-breakpoint-down(md) {
			float: left;
			line-height: 1.3; } } }
