.bannerwrap {
	.hermes {
		.tp-bullet {
			width: 14px;
			height: 14px;
			box-shadow: none;
			background: none;
			border: 2px solid $white;

			&.selected {
				&:after {
					width: 6px;
					height: 6px;
					bottom: 2px;
					left: 2px;
					right: unset;
					background: $white;
					@include bor-rad(50%);
					box-shadow: none !important; } } } }
	.uranus.tparrows {
		background: none !important;
		@include bor-rad(50%);
		&:before {
			@include fs(24rem); }
		// 	color: $green
		&:hover {
			background: color-rgba($black,0.1);
			&:before {
				color: lighten($green, 10%); } } }
	.tp-leftarrow {
		@include media-breakpoint-down(xs) {
			left: -20px !important; } }
	.tp-rightarrow {
		@include media-breakpoint-down(xs) {
			left: calc(100% + 20px) !important; } }
	.banner-caption {
		background: rgba(0,0,0,0.25);
		padding: 20px 25px;
		color: $white;
		font-weight: 300;
		@include fs(30rem);
		line-height: 1.5;
		text-align: center;
		font-weight: 300;
		text-transform: uppercase;
		@include media-breakpoint-down(sm) {
			font-size: 18px !important; }

		a {
			color: lighten($green, 15%);
			@include fs(16rem);
			text-transform: uppercase;
			font-weight: 600 !important;
			display: block;
			text-align: center;
			@include transition(all 0.3s);


			&:hover {
				color: $white; }
			&:before {
				content: '';
				display: block;
				margin: 10px auto;
				height: 1px;
				width: 110px;
				background: $white; } } }

