	.btn-showmenu {
		display: none;
		position: relative;
		float: left;
		width: 30px;
		height: 18px;
		cursor: pointer;
		z-index: 10;
		margin-top: 11px;
		@include media-breakpoint-down(lg) {
			display: block; }
		&:before {
			content: '';
			@include pos(abs, 0 null null 0);
			width: 22px;
			height: 2px;
			background: $main;
			@include transition(all 0.2s); }

		&:after {
			content: '';
			@include pos(abs, calc(100% - 2px) null null 0);
			width: 22px;
			height: 2px;
			background: $main;
			@include transition(all 0.2s); }
		span {
			&:before {
				content: '';
				@include pos(abs, calc(50% - 1px) null null 0);
				width: 18px;
				height: 2px;
				background: $main;
				@include transition(all 0.2s); } } }
	.overlay {
		@include pos(fixed, 0 0 0 0);
		background: rgba(0,0,0,0.7);
		z-index: 80;
		display: none; }
	.searchtoggle {
		float: left;
		@include fs(20rem);
		color: $main;
		margin-left: 20px;
		cursor: pointer;
		margin-top: 6px;
		span {
			font-weight: 700; }
		&:hover {
			color: $green; } } }


