.homepage {
	.news-list {
		padding: 0 !important; }
	.pagetitle {
		text-transform: uppercase; } }
.pagetitle {
	@include fs(30rem);
	font-weight: 400;
	color: $main;
	margin-bottom: 30px;
	position: relative;
	@include media-breakpoint-down(sm) {
		margin-bottom: 15px;
		@include fs(24rem); }
	&.center {
		text-align: center;
		margin-bottom: 40px;
		&:after {
			@include pos(abs, null null -10px calc(50% - 25px)); } }
	&:after {
		content: '';
		display: block;
		width: 50px;
		height: 1px;
		background: $main;
		margin-top: 5px; } }

.home-lastnews {
	padding: 50px 0 30px;
	@include media-breakpoint-up(md) {
		padding: 30px 0 10px; }
	@include media-breakpoint-up(sm) {
		padding-bottom: 0; } }
.news-list {
	.news-item {
		&.item-big {
			@include media-breakpoint-up(md) {
				figure {
					position: relative;
					&:before {
						content: '';
						@include pos(abs, 0 0 0 0);
						@include rotate(-2deg);
						box-shadow: 0 2px 20px rgba(0,0,0,0.15);
						background: $green; }
					.item-img {
						display: flex;
						@include flex;
						align-items: center;
						justify-content: center;
						height: 443px;
						overflow: hidden;
						@include media-breakpoint-down(lg) {
							height: 380px; } }
					figcaption {
						@include pos(abs, null 0 0 0);
						z-index: 20;
						background: -webkit-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3), rgba(0,0,0,0.5));
						background: -o-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3), rgba(0,0,0,0.5));
						background: -moz-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3), rgba(0,0,0,0.5));
						background: linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.3), rgba(0,0,0,0.5));
						display: flex;
						align-items: flex-end;
						.item-title {
							float: left;
							width: 40%;
							margin-bottom: 0;
							min-height: unset;
							a {
								color: $white;
								@include fs(24rem);
								@include media-breakpoint-down(md) {
									@include fs(20rem); } } }
						.wrap-up.clearfix {
							float: left;
							width: 35%;
							margin-bottom: 0;
							padding-left: 3%;
							.item-author {
								width: calc(100% - 60px);
								.img {
									img {} }
								.text {

									.name {
										color: $white; }
									time {
										color: $white; } } }
							.item-button {
								width: 50px;
								a {
									background: none;
									color: $white;
									border: 1px solid $white;
									&:hover {
										background: $green;
										color: $white;
										border: 1px solid $green; } } } }
						.wrap-bottom {
							float: left;
							width: 25%;
							text-align: right;
							border: none;
							padding-left: 10px;
							ul.item-categories {
								padding-top: 0;
								li {
									a {
										color: $white; }
									&:before {
										color: $white; } } }
							.item-likeshare {
								float: right;
								border: none;

								span {
									color: $white; } } } } } } } } }
.home-hotnews {
	padding: 0px 0 40px; }
.hot-news-list {
	margin: 0 -10px;
	.news-col {
		width: 25%;
		float: left;
		@include media-breakpoint-down(md) {
			width: 100%; }
		&:nth-child(2) {
			width: 50%;
			@include media-breakpoint-down(md) {
				width: 100%; } }
		&:nth-child(3) {
			@include media-breakpoint-down(md) {
				width: 100%; } } }
	.news-item {
		padding: 10px;
		float: left;
		width: 100%;
		&.tall {
			.item-img {
				height: 620px;
				@include media-breakpoint-down(lg) {
					height: 520px; } } }
		&.width50 {
			width: 50%;
			@include media-breakpoint-down(sm) {
				width: 100%; } }
		figure {
			position: relative;
			float: left;
			width: 100%;
			overflow: hidden;
			.item-img {
				display: block;
				width: 100%;
				height: 300px;
				float: left;
				@include media-breakpoint-down(lg) {
					height: 250px; }
				@include media-breakpoint-down(md) {
					height: 300px; }
				@include media-breakpoint-down(xs) {
					height: 270px; } }
			&:hover {
				.item-img {
					@include transform(scale(1.1,1.1)); } }
			figcaption {
				padding: 50px 20px 20px;
				@include pos(abs, null 0 0 0);
				background: -webkit-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.4), rgba(0,0,0,0.6));
				background: -o-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.4), rgba(0,0,0,0.6));
				background: -moz-linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.4), rgba(0,0,0,0.6));
				background: linear-gradient(rgba(0,0,0,0.0), rgba(0,0,0,0.4), rgba(0,0,0,0.6));
				.item-title {
					display: block;
					margin-bottom: 10px;
					a {
						@include fs(18rem);
						color: $white;
						font-weight: 700;
						display: -webkit-box;
						-webkit-line-clamp: 3;
						-webkit-box-orient: vertical;
						overflow: hidden;
						&:hover {
							color: lighten($green, 15%); } } }
				.item-des {
					color: $white;
					@include fs(14rem);
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
					overflow: hidden; } } } } }

.home-banner-era {
	padding: 30px 0;
	background: #000;
	.banner-content {
		text-align: center; } }

.home-author {
	padding: 50px 0;
	@include media-breakpoint-up(md) {
		padding: 30px 0; }
	@include media-breakpoint-up(sm) {
		padding: 20px 0; }
	.home-author-list {
		.author-item {
			float: left;
			width: 20%;
			padding: 0 10px;
			@include media-breakpoint-down(md) {
				width: 33.33%; }
			@include media-breakpoint-down(sm) {
				width: 50%; }
			@include media-breakpoint-down(xs);
			figure {
				margin-bottom: 30px;
				.item-img {
					display: block;
					width: 130px;
					height: 130px;
					@include bor-rad(50%);
					overflow: hidden;
					margin: auto;
					margin-bottom: 20px;
					img {
						@include transition(all 0.3s); }
					&:hover {
						img {
							@include transform(scale(1.1,1.1)); } } }
				figcaption {
					text-align: center;
					h3.item-name {
						display: block;
						margin-bottom: 7px;
						a {
							@include fs(14rem);
							color: $main;
							text-transform: uppercase;
							font-weight: 600;
							&:hover {
								color: $green; } } }
					.author-des {
						&:after {
							content: '\f105';
							font-family: 'fontawesome';
							color: lighten($green, 10%);
							margin-left: 5px; } } } } } } }
.home-banner-dns {
	.banner-content {
		margin-bottom: 50px;
		text-align: center; } }

.home-subcribe {
	padding: 40px 0;
	background: url(../img/constant/bg-subcribe.jpg) center center no-repeat;
	background-size: cover;
	text-align: center;
	.title {
		@include fs(18rem);
		text-transform: uppercase;
		margin-bottom: 5px;
		color: $white; }
	.subscribe {

		.module-title {
			@include fs(14rem);
			color: $white;
			margin-bottom: 15px; }
		.subscribefrm {
			@include bor-rad(30px);
			overflow: hidden;
			max-width: 380px;
			margin: auto;
			input {
				float: left;
				height: 36px;
				line-height: 36px;
				padding: 0 20px;
				width: calc(100% - 85px);
				border: none; }
			.subscribebutton {
				float: left;
				height: 36px;
				line-height: 36px;
				width: 85px;
				background: darken($green, 5%);
				color: $white;
				text-align: center;
				@include fs(0);
				&:before {
					content: 'Sent';
					text-transform: uppercase;
					@include fs(16rem);
					font-weight: 300; }
				&:hover {
					background: lighten($green, 10%); } } } } }


