.Author {
	.guestbook-list {
		padding-bottom: 0 !important;
		margin-bottom: 0 !important; } }
.modal {
	&::-webkit-scrollbar {
		display: none; }
	.modal-header {
		padding: 0;
		position: relative;
		.close {
			@include pos(abs, 0 20px null null);
			@include fs(30rem); } }
	.modal-title {
		@include fs(20rem);
		width: 100%;
		text-align: center;
		margin: 20px 0; }
	.modal-body {
		padding: 30px;
		.content {
			.first {
				&:first-letter {
					@include fs(71rem);
					float: left;
					line-height: 65px;
					margin-right: 10px; } } }

		.btn-buybook {
			text-align: center;
			width: 100%;
			@include transition(all 0.5s);
			margin: 20px 0;
			a {
				text-transform: uppercase;
				display: inline-block;
				padding: 6px 20px;
				background: $green;
				color: $white;
				cursor: pointer;
				&:hover {
					background: darken($green, 10%); } } } }
	.modal-dialog {
		@include media-breakpoint-up(lg) {
			max-width: 800px; } }
	.modal-content {
		@include media-breakpoint-up(lg) {
			min-height: 90vh; } } }
.author-list {
	padding: 50px 0;
	.author-item {
		figure {
			position: relative;
			margin-bottom: 30px;
			@include media-breakpoint-down(md) {
				margin-bottom: 15px; }
			.item-img {
				display: block;
				position: relative;
				&:before {
					content: '';
					@include pos(abs, 7px 7px 7px 7px);
					border: 1px solid $white;
					z-index: 10; }
				&:after {
					content: '';
					@include pos(abs, 7px 100% 7px 7px);
					background: rgba(255,255,255,0.6);
					@include transition(all 0.5s);
					z-index: 11;
					opacity: 0; }
				&:hover {
					&:after {
						right: 7px;
						opacity: 1; }
					.btn-moreinfo {
						@include transform(translate(-50%, -50%) scale3d(1,1,1)); } }

				.btn-moreinfo {
					@include pos(abs, 50% null null 50%);
					@include transform(translate(-50%, -50%) scale3d(0,0,0));
					z-index: 20;
					display: inline-block;
					padding: 10px 20px;
					color: $white;
					background: #000;
					@include transition(all 0.3s);
					&:hover {
						background: $green; } } }
			figcaption {
				padding: 20px 0;
				.item-name {
					margin-bottom: 5px;
					a {
						font: 300 20px/19px "Roboto Slab", serif;
						color: darken($main, 5%);
						@include fs(20rem);
						position: relative;
						@include media-breakpoint-down(md) {
							@include fs(18rem); }
						&:before {
							content: "";
							width: 22px;
							height: 1px;
							background: $main;
							display: inline-block;
							margin-right: 10px;
							@include media-breakpoint-down(md) {
								display: none; }
							@include media-breakpoint-down(xs) {
								display: inline-block; } }
						&:hover {

							color: $green; } } }

				.item-des {
					color: #999;
					@include fs(13rem);
					padding-left: 30px;
					font-weight: 300;
					margin-bottom: 10px;
					@include media-breakpoint-down(md) {
						padding-left: 0; } }
				.item-social {
					padding-left: 30px;
					@include media-breakpoint-down(md) {
						padding-left: 0; }
					a {
						color: $main;
						display: inline-block;
						@include fs(16rem);
						+ a {
							margin-left: 24px; }
						&:hover {
							@include transform(scale(1.2,1.2));
							color: $green; } } } } } } }

.author-detail-wrap {
	padding: 50px 0 20px;
	@include media-breakpoint-down(sm) {
		padding: 30px 0 0; }
	.col-right {
		padding-left: 40px;
		@include media-breakpoint-down(md) {
			padding-left: 15px; } }
	.author-info {
		margin-bottom: 20px;
		figure {
			overflow: hidden;
			position: relative;
			@include bor-rad(50%);
			.item-img {
				width: 100%;
				display: block;
				overflow: hidden;
				img {
					width: 100%; } }
			figcaption {
				@include pos(abs, null 0 0px 0);
				text-align: center;
				background: color-rgba($green, 0.9);
				padding: 5px 0;
				.item-name {
					color: $white;
					@include fs(20rem);
					padding-bottom: 5px;
					position: relative;
					margin-bottom: 10px;
					&:before {
						content: '';
						@include pos(abs, null null 0 calc(50% - 25px));
						height: 1px;
						width: 50px;
						background: $white; } }

				.item-post {
					margin-top: 5px;
					color: $white;
					@include fs(14rem);
					span {
						display: inline-block;

						@include transform(translateY(-2px));
						margin-right: 5px; }
					.number {
						display: inline-block; } }

				.item-social {
					a {
						color: $white;
						display: inline-block;
						margin: 0 8px;
						@include fs(15rem);
						&:hover {
							@include transform(scale(1.2,1.2));
							color: lighten($green, 30%); } } } } } }
	.author-img-slide {
		overflow: hidden;
		.item {
			img {
				width: 100%; } }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null 0);
			font-size: 0;
			@include transform(translate(-100%, -50%));
			z-index: 10;
			&:before {
				content: '\e879';
				font-family: 'linearicons-free';
				@include fs(20rem);
				width: 35px;
				height: 35px;
				line-height: 35px;
				background: rgba(255,255,255,0.9);
				text-align: center;
				display: inline-block;
				@include transition(all 0.3s); } }
		&:hover {
			.slick-prev, .slick-next {
				@include transform(translate(0, -50%)); } }
		.slick-next {
			left: unset;
			right: 0;
			@include transform(translate(100%, -50%));
			&:before {
				content: '\e87a'; } } }
	.news-list {
		padding: 30px 0 0; } }
.author-nav {
	background: $white;
	padding: 20px 0;
	margin-top: 50px;
	@include media-breakpoint-down(md) {
		margin-top: 10px; }
	h1.author-name {
		margin-bottom: 10px;
		color: $main;
		@include fs(30rem);
		font-weight: 300;
		display: inline-block;
		margin-right: 50px;

		&:before {
			content: "";
			width: 22px;
			height: 1px;
			background: $main;
			display: inline-block;
			margin-right: 10px; } }

	ul {
		display: inline-block;
		li {
			position: relative;
			display: inline-block;
			a {
				color: $main;
				font-weight: 300;
				@include fs(20rem);
				margin: 0 30px;
				@include media-breakpoint-down(xs) {
					@include fs(16rem);
					margin: 0 15px; }
				&:hover {

					color: $green; } }
			&.active {
				a {
					font-weight: 700;
					@include fs(22rem);
					color: $main !important;
					@include media-breakpoint-down(xs) {
						@include fs(18rem); } } }
			&:last-child {
				&:after {
					display: none; } }
			&:after {
				content: '';
				@include pos(abs, 4px 0 null null);
				width: 1px;
				height: 25px;
				background: $main;
				@include media-breakpoint-down(xs) {
					height: 18px; } } } } }


.books-list {
	padding: 30px 0 0;
	.book-item {
		figure {
			padding: 10px;
			background: $white;
			margin-bottom: 40px;
			position: relative;
			overflow: hidden;
			.item-img {
				display: flex;
				justify-content: center;
				align-items: center;
				position: relative;
				&:before {
					content: '';
					@include pos(abs, 0 0 0 0);
					background: rgba(255,255,255,0.9);
					@include transition(all 0.3s);
					opacity: 0; }
				img {} }
			figcaption {
				padding: 20px 0;
				@include pos(abs, 50% 0 null 0);
				@include transform(translateY(-50%));
				@include transition(all 0.3s);

				h2.item-name {
					text-align: center;
					width: 100%;
					margin-bottom: 15px;
					opacity: 0;
					@include transform(translateY(-10px));
					@include transition(all 0.3s);
					a {
						color: $main;
						font-weight: {}
						@include fs(18rem);
						line-height: 1.3;
						margin-bottom: 15px;
						min-height: 46px; } }
				.item-button {
					text-align: center;
					width: 100%;
					opacity: 0;
					@include transform(translateY(10px));
					@include transition(all 0.5s);
					a {
						text-transform: uppercase;
						display: inline-block;
						padding: 6px 20px;
						background: #000;
						color: $white;
						cursor: pointer;
						&:hover {
							background: $green; } } } }

			&:hover {
				.item-img {
					&:before {
						opacity: 1; } }
				figcaption {

					.item-name {
						opacity: 1;
						@include transform(translate(0)); }

					.item-button {
						opacity: 1;
						@include transform(translate(0)); } } } } } }
