.login-page {
	// background: darken($white,3%)
	.login-panel {
		margin: 60px 0;
		background: $white;
		// box-shadow: 0 0 50px color-rgba($black,0.1)
		@include bor-rad(5px);
		@include media-breakpoint-down(lg) {
			margin: 60px 0; } }
	.sitelogo {
		text-align: center;
		margin-bottom: 40px;
		width: 100%;
		img {
			height: 80px; }
		@include media-breakpoint-down(xs) {
			margin-bottom: 30px; } }
	.headtitle {
		@include fs(18rem);
		font-weight: 700;
		text-align: center;
		margin-bottom: 30px;
		color: $lblack;
		text-transform: uppercase;
		&:after {
			content: '';
			margin: 10px auto;
			display: block;
			width: 50px;
			height: 2px;
			background: $green;
			margin-top: 10px; } } }
.login-form {
	width: 100%;
	margin: 0 auto;
	.panel-heading {
		display: none; }
	.title {
		@include fs(24rem);
		font-weight: 500;
		text-transform: uppercase;
		border-left: 3px solid $red;
		padding-left: 10px;
		margin-bottom: 30px; }
	.module-title {
		@include fs(14rem);
		color: darken($white,60%);
		margin-bottom: 20px;
		a {
			color: $red;
			&:hover {
				lighten($red,10%) {} } } }
	.form-group {
		margin-bottom: 20px;
		@include pos(rel,null);
		label {
			color: darken($white,40%);
			text-transform: uppercase;
			@include fs(0rem);
			display: block;
			width: 50px;
			height: 40px;
			line-height: 40px;
			text-align: center;
			@include pos(abs, 6px null null 0);
			border-right: 1px solid darken($white,10%);
			// display: none
			.fa {
				@include fs(18rem);
				line-height: 40px; } }
		input {
			width: 100%;
			@include fs(18rem);
			border: 1px solid darken($white,15%);
			@include bor-rad(5px);
			padding: 15px 30px 15px 60px;
			@include transition(all,0.5s);
			&:active, &:hover, &:focus {
				border-color: darken($white,40%); } }
		a {
			display: inline-block;
			color: $black;
			padding: 10px 0;
			margin-right: 15px;
			&:hover {
				color: $red; } } }
	.forget {
		@include fs(14rem);
		color: darken($white,40%);
		margin-bottom: 30px;
		input[type='checkbox'] {
			display: none;
			+ label {
				color: darken($white,60%);
				font-weight: 300;
				width: auto;
				height: auto;
				text-align: left;
				padding-left: 30px;
				line-height: 1.5;
				@include fs(14rem);
				text-transform: none;
				@include pos(rel,null);
				@include transition(all,0.5s);
				&:hover {
					color: $red; }
				&:before {
					content: '';
					display: block;
					width: 16px;
					height: 16px;
					border: 1px solid darken($white,40%);
					opacity: 0.5;
					@include pos(abs, 1px null null 0);
					@include transition(all,0.5s); }
				&:after {
					content: '\f00c';
					font-family: 'fontawesome';
					display: block;
					line-height: 14px;
					@include fs(14rem);
					color: $red;
					opacity: 0;
					@include pos(abs, 2px null null 1px);
					@include transition(all,0.5s); } }
			&:checked {
				+ label {
					&:before, &:after {
						opacity: 1; } } } } }
	.btn-login {
		margin: 0 auto;
		width: 100%;
		text-align: center;
		@include pos(rel,null);
		color: $white;
		background: $green;
		display: block;
		@include bor-rad(5px);
		overflow: hidden;
		@include media-breakpoint-down(xs) {
			width: 100%; }
		&:before {
			background: darken($green,10%); }
		&:after {
			content: '\f105';
			font-family: 'fontawesome';
			@include pos(abs, 12px 15px null null); } }
	input[type='reset'] {
		display: none; }
	input[type="submit"] {
		width: 100%;
		color: $white;
		background: none;
		padding: 15px 60px;
		border: none;
		text-transform: uppercase;
		@include fs(16rem); }
	.go-signup {
		text-align: center;
		margin-top: 15px;
		a {
			display: block;
			clear: both;
			color: $red;
			&:hover {
				color: $black; } } } }
