.signup-bg {
	width: 40%;
	@include pos(rel,null);
	overflow: hidden;
	padding: 50px;
	@include media-breakpoint-down(md) {
		width: 100%;
		order: 1; }
	@include media-breakpoint-down(sm) {
		padding: 30px; }
	.signup-img {
		@include pos(abs, 0 null 0 0);
		right: 0;
		.signup-overlay {
			display: block;
			@include pos(abs, 0 null 0 0);
			right: 0;
			background: $black;
			opacity: 0.5;
			z-index: 1; }
		img {
			height: 100%;
			width: auto;
			max-width: unset;
			filter: grayscale(100%);
			@include media-breakpoint-down(md) {
				height: auto;
				width: 100%; }
			@include media-breakpoint-down(sm) {
				@include transform(scale3d(1.1,1.1,1)); }
			@include media-breakpoint-down(xs) {
				width: auto;
				height: 100%; } } }
	.signup-des {
		@include pos(rel,null);
		z-index: 1;
		color: $white;
		.title {
			@include fs(46rem);
			font-weight: 300;
			margin-bottom: 20px;
			// opacity: 0.8
			@include media-breakpoint-down(sm) {
				@include fs(36rem);
				margin-bottom: 10px; } }
		.module-title {
			@include fs(26rem);
			font-weight: 200;
			opacity: 0.9;
			@include media-breakpoint-down(sm) {
				@include fs(20rem); } }
		.des {
			@include fs(20rem);
			span {
				opacity: 0.8;
				font-weight: 200;
				display: inline-block;
				margin-right: 10px; }
			a {
				display: inline-block;
				opacity: 1;
				font-weight: 300;
				@include fs(24rem);
				color: $white;
				@include media-breakpoint-down(xs) {
					@include fs(26rem); }
				&:after {
					content: '\f105';
					font-family: 'fontawesome';
					margin-left: 5px; }
				&:hover {
					color: $green; } } } } }


// USER SIDEBAR
.col-static {
	position: static; }
.user-sidebar {
	margin: 40px 0 20px;
	@include pos(rel,null);
	.userinfo {
		// background: $green
		border: 1px dashed $green;
		background: lighten($green,60%);
		@include bor-rad(3px);
		margin-bottom: 20px;
		figure {
			&:after {
				content: '';
				display: table;
				width: 100%;
				clear: both; }
			.user-img {
				display: none;
				width: 80px;
				float: left;
				@include media-breakpoint-down(lg) {
					width: 60px; } }
			figcaption {
				padding: 10px 15px;
				@include media-breakpoint-down(lg) {
					padding: 7px 15px; }
				.title {
					color: $main;
					opacity: 0.8;
					font-weight: 400;
					@include fs(13rem); }
				.user-name {
					text-transform: uppercase;
					font-weight: 600;
					@include fs(16rem);
					color: $black;
					margin-bottom: 5px; }
				.level {
					.fa {
						margin-right: 5px; } } } } } }

.user-sidelink {
	background: darken($white, 3%);
	border: 1px solid darken($white,15%);
	@include bor-rad(3px);
	margin-bottom: 60px;
	@include media-breakpoint-down(md) {
		margin-bottom: 30px;
		display: block; }
	> ul {
		padding: 10px 0;
		> li {
			width: 100%;
			background: darken($white, 3%) !important;
			position: relative;
			&:before {
				background: lighten($green,60%); }
			&:hover {
				background: none !important;
				color: $main; }
			> a {
				display: block;
				color: $main;
				padding: 0px 20px;
				line-height: 40px;
				text-transform: uppercase;
				@include fs(13rem);
				font-weight: 600;
				&:hover {
					color: $green; }

				.btn-showsub {} }

			.btn-showsub {
				@include pos(abs, 3px 0 null null);
				@include fs(20rem);
				font-weight: 700;
				color: $main;
				padding: 0 15px;
				cursor: pointer;
				@include transition(all 0.2s);
				&:hover {
					color: $green; }
				&.active {
					@include rotate(90deg); } }
			.sub {
				display: none;
				ul {
					// padding-left: 5px
					li {

						a {
							@include fs(14rem);
							font-weight: 400;
							&:hover {
								color: $green; } } } } }
			&.active {
				> a {
					color: $green; }
				.sub {
					display: block; } } } } }

// user page

.user-page {
	margin: 40px 0;
	@include pos(rel,null);
	@include media-breakpoint-down(md) {
		margin-top: 0; }
	.table {
		border-bottom: 1px solid darken($white,10%);
		@include fs(14rem);
		margin-bottom: 0;
		border: 1px solid darken($white,20%);
		// +bor-rad(5px)
		overflow: hidden;
		a {
			color: $main;
			font-weight: 700;
			// &:before
			// 	content: '\f00e'
			// 	font-family: 'fontawesome'
			// 	margin-right: 5px
			&:hover {
				color: $green; } }
		th {
			border: none;
			// background: lighten($red,45%)
			background: $green;
			color: $white;
			white-space: nowrap; }
		td {
			border: none;
			&:first-child {
				color: $main;
				font-weight: 700;
				text-align: center; }
			.edit {
				float: left;
				margin-right: 10px;
				@include fs(16rem);
				cursor: pointer;
				&:hover {
					color: $green; } }

			a {
				float: left;
				margin-right: 10px; } }

		th, td {
			white-space: nowrap;
			&:nth-child(3) {
				white-space: normal;
				@include media-breakpoint-down(sm) {
					white-space: nowrap; } }
			&:nth-child(3), &:nth-child(4) {
				text-align: center; }
			&:last-child {
				text-align: right; } }
		tr {
			&:nth-child(even) {
