.guestbook-page .container {
  max-width: 960px;
}

.guestbook-page:before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  background: url("../img/guestbook/ic1.png") top right no-repeat;
  width: 450px;
  height: 450px;
  background-size: 100%;
}

.guestbook-page:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  background: url("../img/guestbook/ic2.png") bottom left no-repeat;
  width: 400px;
  height: 400px;
  background-size: 100%;
}

.guestbook-list {
  margin: 70px 0 100px;
}

@media (max-width: 991px) {
  .guestbook-list {
    margin: 50px 0 100px;
  }
}

.guestbook-list .guestbook-item {
  margin-bottom: 40px;
  padding-bottom: 30px;
  border-bottom: 1px dashed #999999;
  position: relative;
  z-index: 100;
}

.guestbook-list .guestbook-item:last-child {
  border: none;
}

.guestbook-list .guestbook-item figure .item-img {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden;
  width: 150px;
  height: 150px;
  float: left;
}

@media (min-width: 768px) and (max-width: 991px) {
  .guestbook-list .guestbook-item figure .item-img {
    width: 130px;
    height: 130px;
  }
}

@media (max-width: 767px) {
  .guestbook-list .guestbook-item figure .item-img {
    margin: auto;
    margin-bottom: 20px;
    float: none;
  }
}

.guestbook-list .guestbook-item figure .item-img img {
  width: 100%;
}

.guestbook-list .guestbook-item figure figcaption {
  padding-left: 30px;
  float: left;
  width: calc(100% - 150px);
}

@media (max-width: 1199px) {
  .guestbook-list .guestbook-item figure figcaption {
    width: 100%;
    float: none;
    display: block;
    padding-left: 0;
  }
}

.guestbook-list .guestbook-item figure figcaption .item-content {
  margin-bottom: 20px;
}

@media (max-width: 543px) {
  .guestbook-list .guestbook-item figure figcaption .item-content {
    text-align: center;
  }
}

.guestbook-list .guestbook-item figure figcaption time {
  float: left;
  letter-spacing: 2px;
  color: #4d4d4d;
}

@media (max-width: 543px) {
  .guestbook-list .guestbook-item figure figcaption time {
    display: block;
    float: none;
    margin-bottom: 10px;
    text-align: center;
  }
}

.guestbook-list .guestbook-item figure figcaption .item-name {
  float: right;
  color: #666666;
  font-size: 1.25rem;
  font-weight: 300;
  text-align: right;
}

@media (max-width: 543px) {
  .guestbook-list .guestbook-item figure figcaption .item-name {
    float: none;
    display: block;
    text-align: left;
    text-align: center;
  }
}

.guestbook-list .guestbook-item figure figcaption .item-name:before {
  content: '--';
  display: inline-block;
  margin-right: 5px;
}

.guestbook-list .guestbook-item figure figcaption .item-name:after {
  content: '--';
  display: inline-block;
  margin-right: 5px;
}

.guestbook-private .guestbook-list {
  background: #fff;
  padding: 50px 100px;
  margin-top: 30px;
}

@media (max-width: 1199px) {
  .guestbook-private .guestbook-list {
    padding: 50px 20px;
  }
}
