.breadcrumb-wrap {
	@include pos(rel,null);
	background: darken($white, 3%);
	.breadcrumb-bg {
		@include pos(rel,null);
		height: 400px;
		.breadcrumb-overlay {
			@include pos(abs, 0 null 0 0);
			right: 0;
			background: rgba(0,0,0,0.1); } }
	.pagename {
		color: $white;
		@include fs(32rem);
		margin-bottom: 10px;
		display: none;



		@include media-breakpoint-down(md) {
			@include fs(28rem);
			padding: 30px 0; } }
	.breadcrumb-content {
		// +pos(abs, 50% null null 50%)
 }		// +transform(translate(-50%, -50%))


	.breadcrumb {
		width: 100%;
		margin: 0;
		// margin-bottom: 15px
		padding: 9px 0;
		background: none;
		@include bor-rad(0);
		display: inline-block;
		color: $white;
		// border-bottom: 1px solid darken($white,20%)
		li {
			display: inline-block;
			color: $main;
			@include media-breakpoint-down(md) {
				@include fs(13rem); }
			&:after {
				content: '\e876';
				font-family: 'Linearicons-free';
				@include fs(12rem);
				font-weight: 600;
				margin: 0 3px 0 5px;
				color: $main; }
			&:last-child {
				&:after {
					display: none; } }
			a {
				color: $main;
				font-weight: 600;
				span {
 }					// font-weight: 300
				&:hover {
					color: $green;
					font-weight: 900;
 } } } } }					// text-decoration: underline

