.pages {
	text-align: center;
	.pagination {
		text-align: center;
		li {
			display: inline-block;
			margin: 0 3px;
			@include transition(all 0.2s);
			a, span {
				display: inline-block;
				color: darken($white,40%);
				width: 36px;
				height: 36px;
				line-height: 36px;
				background: $white; }

			&.active {

				a,span {
					color: $green;
					@include fs(18rem);
					font-weight: 700; } }
			&:hover {
				@include transform(translateY(-5px));
				a,span {
					color: $green; } } } } }
