.about-menu {
	ul {
		text-align: center;
		border-bottom: 1px solid darken($white, 20%);
		margin-bottom: 50px;

		li {
			display: inline-block;
			margin: 0 30px;

			a {
				color: darken($white, 60%);
				font-weight: 600;
				display: inline-block;
				@include fs(16rem);
				padding: 12px 0;
				position: relative;
				&:before {
					content: '';
					@include pos(abs, null 51% -1px 51%);
					height: 2px;
					background: lighten($green, 20%);
					@include transition(all 0.2s); }
				&:hover {
					&:before {
						left: 0;
						right: 0; } } }
			&.active {
				a {
					color: $pink;
					&:before {
						left: 0;
						right: 0; } } } } } }
.about-intro {
	padding: 50px 0;
	.about-slide {
		margin-bottom: 15px; }
	.about-nav {
		margin: 0 25px;
		@include media-breakpoint-down(md) {
			margin-bottom: 30px; }
		.item {
			padding: 0 5px;
			img {
				border: 2px solid transparent; }
			&.slick-current {
				img {
					border: 2px solid $green; } } }
		.slick-prev, .slick-next {
			@include pos(abs, 50% null null -25px);
			font-size: 0;
			@include transform(translateY(-50%));
			z-index: 10;
			&:before {
				content: '\e875';
				font-family: 'linearicons-free';
				@include fs(24rem);
				color: darken($white, 40%);
				font-weight: 500;
				@include transition(all 0.3s); }
			&:hover {
				&:before {
					color: lighten($green, 20%); } } }

		.slick-next {
			left: unset;
			right: -25px;
			&:before {
				content: '\e876'; } } } }
.about-number {
	padding: 100px 0;
	position: relative;
	background-attachment: fixed !important;
	@include media-breakpoint-down(md) {
		padding: 50px 0; }
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.pagetitle {
		color: $white;

		&:after {
			background: $white;
			@include pos(abs, null null -10px calc(50% - 25px)); } }

	&:before {
		content: '';
		@include pos(abs, 0 0 0 0);
		background: rgba(18,18,18,0.5); }
	figure {
		margin-top: 30px;
		text-align: center;
		.icon {
			display: flex;
			justify-content: center;
			align-items: center;
			height: 70px;
			@include fs(40rem);
			color: $white; }
		figcaption {
			h5 {
				@include fs(48rem);
				font-weight: 700;
				color: #222;
				color: $white;
				margin-bottom: 10px; }
			h6 {
				color: $white;
				@include fs(13rem);
				text-transform: uppercase; } } } }
.about-title {
	color: $main;
	@include fs(30rem);
	margin-bottom: 20px;
	@include media-breakpoint-down(sm) {
		@include fs(22rem); }

	.content {
		color: #666; } }

.about-vision {
	padding: 50px 0;
	// .about-title
	// 	text-align: center
	@include media-breakpoint-down(sm) {
		padding: 30px 0; }
	.vision {
		margin: 20px 0;
		figure {
			text-align: center;
			.fa {
				@include fs(60rem);
				color: lighten($green, 10%);
				margin-bottom: 20px;
				@include media-breakpoint-down(lg); }

			figcaption {
				h3 {
					text-transform: uppercase;
					@include fs(18rem);
					color: #666;
					font-weight: 600;
					@include media-breakpoint-down(lg) {
						margin-bottom: 10px; } }
				.desc {
					color: #666;
					max-width: 550px;
					margin: auto; } } } } }

.about-partner {
	padding: 50px 0 60px;
	background: $white;
	@include media-breakpoint-down(sm) {
		padding: 30px 0; } }
.partner-slide {
	margin: 20px;
	.partner-item {
		padding: 0 30px;
		@include media-breakpoint-down(lg) {
			padding: 0 15px; }
		figure {
			.item-img {
				display: flex;
				justify-content: center;
				align-items: center;
				height: 150px;
				background: darken($white, 3%);
				border: 1px solid darken($white, 8%);
				@include transition(all 0.5s);
				cursor: pointer;
				&:hover {
					background: darken($white, 6%);
					@include transition(all 0.1s); }

				img {
					max-width: 80%;
					max-height: 70%; } } } }

	.slick-prev, .slick-next {
		font-size: 0;
		@include pos(abs, 50% null null -20px);
		@include transform(translateY(-50%));
		&:before {
			content: '\e875';
			font-family: 'linearicons-free';
			@include fs(24rem);
			color: darken($white, 30%);
			font-weight: 700;
			@include transition(all 0.3s); }
		&:hover {
			&:before {
				color: $green; } } }

	.slick-next {
		left: unset;
		right: -20px;
		&:before {
			content: '\e876'; } } }



.fancybox-placeholder {
		background: $white; }




