		// 			.color
 }		// 				border-color: $blue
	.product-size {
		width: 50%;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-start;
		padding: 15px 0;
		// border-bottom: 1px dashed darken($white,40%)
		.label {
			display: block;
			width: 100%;
			@include fs(14rem);
			margin-bottom: 5px;
			font-weight: 500; }
		.size, li {
			// display: inline-block
			// vertical-align: middle
			margin-right: 5px;
			margin-bottom: 5px;
			a {
				display: block;
				// width: 40px
				min-width: 32px;
				line-height: 30px;
				padding: 0px 10px;
				// +bor-rad(3px)
				@include fs(13rem);
				border: 1px solid darken($white,20%);
				// +bor-rad(3px)
				text-align: center;
				// background: $white
				@include transition(all,0.5s);
				cursor: pointer;
				color: darken($white,40%);
				font-weight: bold;
				// + .size
				// 	margin-left: 5px
				&:hover {
					color: $blue; }
				&.active {
					border-color: $blue;
					color: $blue; } } } }
	.product-price {
		padding: 20px 0;
		.new {
			color: $red;
			@include fs(28rem); } }
	.share {
		margin-bottom: 20px; } }
.product-news {
	margin-bottom: 40px;
	.news-list {
		.newscol {
			margin-bottom: 30px;
			figure {
				.newsimg {
					display: block;
					margin-bottom: 20px;
					img {
						width: 100%; } }
				figcaption {
					.newsname {
						@include fs(20rem);
						margin-bottom: 10px;
						a {
							color: darken($white,60%);
							&:hover {
								color: $main; } } }
					.newsdes {
						@include fs(14rem);
						color: darken($white,60%);
						&:hover {
							color: $red; }
						&:after {
							content: '\f105';
							font-family: 'fontawesome';
							margin-left: 5px;
							color: $red; } } } } } } }
.product-description {
	margin-bottom: 40px; }
.other-product {
	margin-bottom: 40px; }
