.career-intro {
	padding: 50px 0;
	.title-page {
		color: $main; }
	.intro-item {
		margin-bottom: 30px;
		figure {
			position: relative;
			overflow: hidden;
			.intro-img {
				position: relative;
				img {} }
			.intro-title {
				@include pos(abs, null null 15px 0);
				background: color-rgba($red, 0.8);
				padding: 0 15px;
				line-height: 35px;
				z-index: 10;
				color: $white; }
			figcaption {
				@include pos(abs, 0 0 0 0);
				background: rgba(0,0,0,0.7);
				padding: 20px 18px;
				opacity: 0;
				@include transition(all 0.3s);
				@include transform(translateY(50%));
				@include transition(all 0.3s);
				color: $white;
				.intro-des {} }
			&:hover {
				figcaption {
					opacity: 1;
					@include transform(translateY(0%)); } } } }
	table.table {


		thead {
			tr {
				th {
					border: none;
					border-bottom: 2px solid #ccc; } } }
		tbody {
			tr {
				td {
					cursor: pointer;
					border-top: none;
					border-bottom: 1px solid #e1e1e1;
					vertical-align: middle;

					&:nth-child(1) {
						padding-left: 40px;
						position: relative;
						@include transition(all 0.2s);
						font-weight: 600;
						&:before {
							content: '\f105';
							font-family: 'FontAwesome';
							@include fs(20rem);
							@include pos(abs, 50% null null 15px);
							@include transform(translateY(-50%));
							float: left;
							font-weight: 700;
							@include transition(all 0.2s); } }
					&:nth-child(2) {
						width: 10%; }
					&:nth-child(3) {
						width: 10%; } }

				&.tr-content {
					padding: 0 70px;
					td {
						color: $main !important; }
					td {
						padding: 0 0 0 70px;
						border: 0;
						.content {
							display: none;
							margin-top: 30px;
							cursor: auto; }
						&:nth-child(1) {
							&:before {
								display: none; } } }
					h6 {
						font-weight: 600;
						@include fs(18rem);
						color: $main;
						text-transform: uppercase;
						@include media-breakpoint-down(sm) {
							@include fs(16rem); } }
					hr {
						width: 95%;
						margin: 0 auto 15px;
						border-color: #555; } }
				&.tr-even {
					td {
						background: #f2f2f2 !important; } }

				&:hover {
					td {
						&:nth-child(1) {
								color: $pink; } } }
				&.active {
					td {
						&:nth-child(1) {
							color: $pink; } } } } } } }


