.guestbook {
	main {
		background: url('../img/guestbook/bg.jpg') left top; } }

.guestbook-page {
	padding: 50px 30px;
	position: relative;
	@include media-breakpoint-down(xs) {
		padding: 200px 0; }
	.container {
		max-width: 960px; }
	&:before {
		content: '';
		@include pos(abs, 0 0  null null);
		background: url('../img/guestbook/ic1.png') top right no-repeat;
		width: 450px;
		height: 450px;
		background-size: 100%; }
	&:after {
		content: '';
		@include pos(abs, null null 0 0);
		background: url('../img/guestbook/ic2.png') bottom left no-repeat;
		width: 400px;
		height: 400px;
		background-size: 100%; } }

.guestbook-list {
	margin: 70px 0 100px;
	@include media-breakpoint-down(md) {
		margin: 50px 0 100px; }
	@include media-breakpoint-down(sm);
	.guestbook-item {
		margin-bottom: 40px;
		padding-bottom: 30px;
		border-bottom: 1px dashed darken($white, 40%);
		position: relative;
		z-index: 100;
		&:last-child {
			border: none; }
		figure {
			.item-img {
				display: flex;
				align-items: center;
				justify-content: center;
				@include bor-rad(50%);
				overflow: hidden;
				width: 150px;
				height: 150px;
				float: left;
				@include media-breakpoint-only(md) {
					width: 130px;
					height: 130px; }
				@include media-breakpoint-down(sm) {
					margin: auto;
					margin-bottom: 20px;
					float: none; }
				img {
					width: 100%; } }
			figcaption {
				padding-left: 30px;
				float: left;
				width: calc(100% - 150px);
				@include media-breakpoint-down(lg) {
					width: 100%;
					float: none;
					display: block;
					padding-left: 0; }
				.item-content {
					margin-bottom: 20px;
					@include media-breakpoint-down(xs) {
						text-align: center; } }
				time {
					float: left;
					letter-spacing: 2px;
					color: darken($white, 70%);
					@include media-breakpoint-down(xs) {
						display: block;
						float: none;
						margin-bottom: 10px;
						text-align: center; } }
				.item-name {
					float: right;
					color: darken($white, 60%);
					@include fs(20rem);
					font-weight: 300;
					text-align: right;
					@include media-breakpoint-down(xs) {
						float: none;
						display: block;
						text-align: left;
						text-align: center; }
					&:before {
						content: '--';
						display: inline-block;
						margin-right: 5px; }
					&:after {
						content: '--';
						display: inline-block;
						margin-right: 5px; } } } } } }

.guestbook-private {
	.guestbook-list {
		background: $white;
		padding: 50px 100px;
		margin-top: 30px;
		@include media-breakpoint-down(lg) {
			padding: 50px 20px; } } }
